<template>
  <v-navigation-drawer
    v-bind="$attrs"
    class="loads-dispatch-to-driver"
    temporary
    app
    right
    :width="430"
    @input="toggleDispatchDrawer"
  >
    <div class="d-flex flex-column">
      <div class="d-flex align-center justify-space-between pt-5 mb-8 px-8">
        <h3 class="font-weight-medium">
          Dispatch to Driver
        </h3>
        <v-btn
          icon
          @click="closeDispatchDrawer"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </div>

      <div class="d-flex px-8 mb-4">
        <v-text-field
          dense
          outlined
          append-icon="mdi-magnify"
          hide-details
          v-model="search"
        >
          <template #label>
            <span class="d-block font-weight-medium label--text body-2">
              Search...
            </span>
          </template>
        </v-text-field>
      </div>

      <template v-for="driver in filteredDrivers">
        <div
          v-ripple
          :key="driver.id"
        >
          <v-hover v-slot="{ hover }">
            <div class="assign-driver-btn d-flex align-center justify-space-between px-8 py-3">
              <div class="d-flex flex-column">
                <h4 class="body-1 font-weight-medium mb-2">
                  {{ driver.full_name }}
                </h4>

                <span
                  v-if="driver.phone"
                  class="d-flex align-center body-2 mb-1"
                >
                  <v-img
                    class="mr-2"
                    :max-width="15"
                    :src="require('@images/icons/loads/fax.svg')"
                  />
                  {{ driver.phone }}
                </span>

                <span class="d-flex align-center body-2">
                  <v-img
                    class="mr-2"
                    :max-width="15"
                    :src="require('@images/icons/loads/trailer-capacity.svg')"
                  />
                  {{ driver.trailer_capacity }} vehicles trailer
                </span>
              </div>

              <div>
                <v-btn
                  depressed
                  text
                  class="assign-btn font-weight-semi-bold text-none"
                  :class="{ 'primary--text': hover }"
                  @click="() => dispatchLoadToDriver({
                    driverId: driver.id,
                    driverName: driver.full_name,
                    loadId,
                    loadStatus,
                  })"
                >
                  Dispatch
                </v-btn>
              </div>
            </div>
          </v-hover>
        </div>
      </template>
    </div>
  </v-navigation-drawer>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions as driversActions } from '@modules/drivers/constants';
import { actions as loadsActions, mutations as loadsMutations } from '@modules/loads/constants';

const {
  mapActions: mapLoadsActions,
  mapMutations: mapLoadsMutations,
} = createNamespacedHelpers('loads');
const {
  mapActions: mapDriversActions,
  mapState: mapDriversState,
} = createNamespacedHelpers('drivers');

export default {
  name: 'DispatchDrawer',
  props: {
    loadId: {
      type: [String, Number],
      default: null,
    },
    loadStatus: {
      type: String,
      default: '',
    },
    isOpenedFromLoadsList: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      isDispatchDrawerLoadId: null,
      search: '',
    };
  },
  computed: {
    ...mapDriversState(['driversForDispatchDrawer']),
    filteredDrivers() {
      return this.driversForDispatchDrawer.data.filter(
        ({ full_name: fullName, phone }) => fullName.includes(this.search) || phone.includes(this.search),
      );
    },
  },
  methods: {
    clearSearch() {
      this.search = '';
    },
    closeDispatchDrawer() {
      this.$emit('dispatch-drawer:close');
    },
    getDriverPhone(driverId) {
      return this.driversForDispatchDrawer.data.find(({ id }) => id === driverId).phone;
    },
    toggleDispatchDrawer(opened) {
      this.clearSearch();
      this.$emit('dispatch-drawer:toggle', opened);
    },
    dispatchLoadToDriver({
      driverId,
      loadId,
      driverName,
      loadStatus,
    }) {
      this.closeDispatchDrawer();
      this.dispatchToDriver({
        loadStatus,
        driverName,
        driverId,
        loadId,
        deleteFromList: this.isOpenedFromLoadsList,
      })
        .then(() => {
          const nextDriver = {
            driver: driverName,
            driver_phone: this.getDriverPhone(driverId),
          };
          this.updateLoadPropData({ prop: 'data', data: nextDriver });
          this.$notificationSuccess(`Load ${loadId} was dispatched to Driver ${driverName}`);
        })
        .catch(() => {
          this.$notificationError(`Load ${loadId} was not dispatched`);
        });
    },
    ...mapDriversActions({
      getDrivers: driversActions.GET_DRIVERS_FOR_DISPATCH_DRAWER,
    }),
    ...mapLoadsActions({
      dispatchToDriver: loadsActions.DISPATCH_LOAD_TO_DRIVER,
    }),
    ...mapLoadsMutations({
      updateLoadPropData: loadsMutations.UPDATE_LOAD_PROP_DATA,
    }),
  },
  mounted() {
    this.getDrivers({ query: '?status=active,invited' });
  },
};
</script>
