import STATUSES from '@utils/constants/statuses';
import EVENTS from '@utils/constants/events';
import EventBus from '@utils/eventBus';

const generateID = () => Math.ceil(Math.random() * (1000000 - 1) + 1);

const checkArgsOrder = (config) => {
  const { selfClosable } = config;

  if (typeof selfClosable === 'number') {
    return Object.assign(config, {
      selfClosable: true,
      timeout: selfClosable,
    });
  }

  return config;
};

const emitNotification = (config) => {
  let localConfig = config;
  localConfig = checkArgsOrder(localConfig);
  setTimeout(() => {
    EventBus.$emit(EVENTS.NOTIFICATION_EVENT, localConfig);
  }, 500);
};

class Notification {
  constructor({
    type,
    message,
    selfClosable = true,
    timeout = 4250,
  }) {
    let localTimeout = timeout;

    if (!selfClosable) {
      localTimeout = false;
    }

    return {
      type,
      message,
      selfClosable,
      timeout: localTimeout,
      id: generateID(),
    };
  }
}

const notificationSuccess = (message, selfClosable, timeout) => {
  const config = new Notification({
    type: STATUSES.SUCCESS,
    message,
    selfClosable,
    timeout,
  });

  emitNotification(config);
};

const notificationError = (message, selfClosable, timeout) => {
  const config = new Notification({
    type: STATUSES.ERROR,
    message,
    selfClosable,
    timeout,
  });

  emitNotification(config);
};

const notificationWarning = (message, selfClosable, timeout) => {
  const config = new Notification({
    type: STATUSES.WARNING,
    message,
    selfClosable,
    timeout,
  });

  emitNotification(config);
};

export {
  notificationSuccess,
  notificationError,
  notificationWarning,
};
