<script>
import { createNamespacedHelpers, mapState } from 'vuex';
import Logo from '@components/Logo';
import { actions } from '@modules/auth/constants';
import ROLES from '@utils/constants/roles';

const { mapActions } = createNamespacedHelpers('auth');

export default {
  name: 'NavigationMobile',
  components: {
    Logo,
  },
  data: () => ({
    isNavigationOpened: false,
    isNavigationSettingsOpened: false,
    isNavigationCarrierProfileOpened: false,
  }),
  computed: {
    computedPageTitle() {
      return (this.$route.meta.pageTitle && this.$route.meta.pageTitle) || '';
    },
    computedGoBackHandler() {
      return this.$route.meta?.backRoute || undefined;
    },
    isXs() {
      return this.$vuetify.breakpoint.xsOnly;
    },
    showDispatcherProfileLink() {
      return this.userRole === ROLES.DISPATCHER;
    },
    ...mapState(['userRole']),
  },
  watch: {
    $route() {
      this.isNavigationOpened = false;
    },
    isNavigationSettingsOpened(val) {
      if (val) this.isNavigationCarrierProfileOpened = false;
    },
    isNavigationCarrierProfileOpened(val) {
      if (val) this.isNavigationSettingsOpened = false;
    },
    isNavigationOpened(val) {
      document.documentElement.style.overflow = val ? 'hidden' : 'auto';
    },
  },
  mounted() {
    this.setVh();

    window.addEventListener('scroll', this.setVh);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.setVh);
  },
  methods: {
    ...mapActions({
      signout: actions.SIGN_OUT,
    }),
    logOut() {
      this.signout({ vm: this });
    },
    setVh() {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    },
  },
};
</script>

