<template>
  <v-app
    class="app"
    :class="$route.name"
  >
    <v-main :class="backgroundClass">
      <header
        v-if="isHeaderVisible"
        class="header white pa-5 py-sm-3 px-sm-7"
        :class="{ 'box-shadow': !$root.isMobile, 'white': !transparentHeader }"
      >
        <logo
          :kind="logoOptions.kind"
          :max-width="logoOptions.maxWidth"
        />
      </header>

      <slot></slot>
    </v-main>
  </v-app>
</template>

<script>
import Logo from '@components/Logo';

export default {
  name: 'LayoutWithHeader',
  components: {
    Logo,
  },
  computed: {
    transparentHeader() {
      return this.$route.meta.transparentHeader;
    },
    isHeaderVisible() {
      if (this.$route.meta.withHiddenHeaderOnMobile && this.$root.isMobile) {
        return false;
      }

      return true;
    },
    backgroundClass() {
      return this.$root.isMobile ? 'white' : 'page-background';
    },
    logoOptions() {
      return this.$root.isMobile ? ({
        kind: 'default',
        maxWidth: '48',
      }) : ({
        kind: 'full',
        maxWidth: '138',
      });
    },
  },
};
</script>

<style lang="scss" scoped>
  .header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
  }

  .app {
    padding-top: 88px;

    &::v-deep {
      .v-application--wrap {
        min-height: calc(100vh - 88px);
      }
    }

    @media #{map-get($display-breakpoints, 'sm-and-up')} {
      padding-top: 70px;

      &::v-deep {
        .v-application--wrap {
          min-height: calc(100vh - 70px);
        }
      }
    }

    @media(max-width: 599px) {
      &.SignUpCarrier {
        padding-top: 0;
      }

      &::v-deep {
        .v-application--wrap {
          min-height: 0px;
        }
      }
    }
  }
</style>
