import axios from 'axios';
import { mutations, actions } from '@modules/dispatcher/constants';

export default {
  namespaced: true,
  state: () => ({
    dispatcherProfile: {
      data: [],
      loading: false,
      error: false,
    },
    dispatcherPassword: '',
    dispatcherConfirmPassword: '',
  }),
  getters: {},
  mutations: {
    [mutations.UPDATE_DISPATCHER_PROFILE_DATA]: (state, payload) => {
      state.dispatcherProfile = {
        ...state.dispatcherProfile,
        ...payload,
      };
    },
    [mutations.UPDATE_DISPATCHER_PASSWORD]: (state, payload) => {
      state.dispatcherPassword = payload;
    },
    [mutations.UPDATE_DISPATCHER_CONFIRM_PASSWORD]: (state, payload) => {
      state.dispatcherConfirmPassword = payload;
    },
  },
  actions: {
    [actions.GET_DISPATCHER_PROFILE]({ commit }, { id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { loading: true });
        axios.get(`/dispatchers/${id}`, { requestName: 'dispatcher' }).then(({ data }) => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { data });
          resolve(data);
        }).catch(() => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { error: true });
          reject();
        }).finally(() => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { loading: false });
        });
      });
    },
    [actions.SET_DISPATCHER_PROFILE]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { loading: true });

        axios.put('/dispatchers/', data).then(() => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { data });

          resolve();
        }).catch(() => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { error: true });

          reject();
        }).finally(() => {
          commit(mutations.UPDATE_DISPATCHER_PROFILE_DATA, { loading: false });
        });
      });
    },
  },
};
