import axios from 'axios';
import { mutations, actions } from '@modules/settings/constants';

export default {
  namespaced: true,
  state: () => ({
    billingSettings: {
      data: [],
      loading: false,
      error: false,
    },
    driverAppSettings: {
      data: [],
      loading: false,
      error: false,
    },
  }),
  mutations: {
    [mutations.UPDATE_BILLING_SETTINGS]: (state, payload) => {
      state.billingSettings = {
        ...state.billingSettings,
        ...payload,
      };
    },
    [mutations.UPDATE_DRIVERAPP_SETTINGS]: (state, payload) => {
      state.driverAppSettings = {
        ...state.driverAppSettings,
        ...payload,
      };
    },
  },
  actions: {
    [actions.GET_BILLING_SETTINGS]: ({ commit }) => {
      commit(mutations.UPDATE_BILLING_SETTINGS, { loading: true });
      axios.get('carriers/settings/billing', { requestName: 'billing settings' }).then(({ data }) => {
        commit(mutations.UPDATE_BILLING_SETTINGS, { data });
      }).catch(() => {
        commit(mutations.UPDATE_BILLING_SETTINGS, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_BILLING_SETTINGS, { loading: false });
      });
    },
    [actions.GET_DRIVERAPP_SETTINGS]: ({ commit }) => {
      commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { loading: true });
      axios.get('carriers/settings/driver-app', { requestName: 'driver app settings' }).then(({ data }) => {
        commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { data });
      }).catch(() => {
        commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { loading: false });
      });
    },
    [actions.ADD_BILLING_SETTINGS]({ commit }, { data }) {
      commit(mutations.UPDATE_BILLING_SETTINGS, { loading: true });
      return new Promise((resolve, reject) => {
        axios.put('/carriers/settings/billing', data)
          .then((response) => {
            commit(mutations.UPDATE_BILLING_SETTINGS, { data });
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          }).finally(() => {
            commit(mutations.UPDATE_BILLING_SETTINGS, { loading: false });
          });
      });
    },
    [actions.ADD_DRIVERAPP_SETTINGS]({ commit }, { data }) {
      commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { loading: true });
      return new Promise((resolve, reject) => {
        axios.put('/carriers/settings/driver-app', data)
          .then((response) => {
            commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { data });
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          }).finally(() => {
            commit(mutations.UPDATE_DRIVERAPP_SETTINGS, { loading: false });
          });
      });
    },
  },
};
