import VueCookie from 'vue-cookie';
import ROUTE_NAMES from '@utils/constants/route-names';
import ROLES from '@utils/constants/roles';
import STATUSES from '@utils/constants/statuses';
import EVENTS from '@utils/constants/events';
import LOAD_STATUSES from '@utils/constants/loadStatuses';
import LOAD_STATUSES_TITLE from '@utils/constants/loadStatusesTitle';
import objectFactory from '@utils/helpers/objectFactory';
import configureAxios from '@utils/configureAxios';
import { notificationSuccess, notificationError } from '@services/notifications';

export default (constructor) => {
  const localConstructor = constructor;
  localConstructor.prototype.$constants = objectFactory({
    ROUTE_NAMES,
    ROLES,
    STATUSES,
    EVENTS,
    LOAD_STATUSES,
    LOAD_STATUSES_TITLE,
  });
  localConstructor.prototype.$notificationSuccess = (message, selfClosable, timeout) => notificationSuccess(message, selfClosable, timeout);
  localConstructor.prototype.$notificationError = (message, selfClosable, timeout) => notificationError(message, selfClosable, timeout);

  localConstructor.use(VueCookie);
  configureAxios(localConstructor);
  return localConstructor;
};
