import axios from 'axios';
import { mutations, actions } from '@modules/inspection/constants';

export default {
  namespaced: true,
  state: () => ({
    damages: {
      data: [],
      loading: false,
      error: false,
    },
  }),
  mutations: {
    [mutations.UPDATE_DAMAGES_DATA](state, payload) {
      state.damages = {
        ...state.damages,
        ...payload,
      };
    },
  },
  actions: {
    [actions.GET_DAMAGES]({ commit }) {
      commit(mutations.UPDATE_DAMAGES_DATA, { loading: true });
      axios.get('/vehicles/inspection/damages', { requestName: 'vehicle damages' })
        .then(({ data }) => {
          commit(mutations.UPDATE_DAMAGES_DATA, { data });
        })
        .catch((error) => {
          commit(mutations.UPDATE_DAMAGES_DATA, { error });
        })
        .finally(() => {
          commit(mutations.UPDATE_DAMAGES_DATA, { loading: false });
        });
    },
  },
};
