import axios from 'axios';
import ROLES from '@utils/constants/roles';
import { mutations, actions, getters } from '@modules/profile/constants';
import { notificationError } from '@services/notifications';

export default {
  namespaced: true,
  state: () => ({
    carrierInfo: {
      data: [],
      loading: false,
      error: false,
    },
    carrierInsurance: {
      data: [],
      loading: false,
      error: false,
    },
    carrierCertificates: {
      data: [],
      loading: false,
      error: false,
    },
  }),
  getters: {
    [getters.GET_LOADING]: (state) => state.carrierInfo.loading || state.carrierInsurance.loading || state.carrierCertificates.loading,
  },
  mutations: {
    [mutations.UPDATE_CARRIER_INFO]: (state, payload) => {
      state.carrierInfo = {
        ...state.carrierInfo,
        ...payload,
      };
    },
    [mutations.UPDATE_CARRIER_INSURANCE]: (state, payload) => {
      console.log(payload);
      state.carrierInsurance = {
        ...state.carrierInsurance,
        ...payload,
      };
      console.log(state.carrierInsurance);
    },
    [mutations.UPDATE_CARRIER_CERTIFICATES]: (state, payload) => {
      state.carrierCertificates = {
        ...state.carrierCertificates,
        ...payload,
      };
    },
  },
  actions: {
    // eslint-disable-next-line
    [actions.UPDATE_PASSWORD](_, { password, new_password, confirm_new_password, role }) {
      return new Promise((resolve, reject) => {
        const url = role === ROLES.CARRIER ? `/${role}s/info` : '/dispatchers/';
        axios.put(url, { password, new_password, confirm_new_password })
          .then(() => {
            resolve();
          }).catch((error) => {
            if (error.response.data.detail) {
              reject(error.response.data.detail);
            } else {
              reject(error.response.data[0]);
            }
          });
      });
    },
    [actions.GET_CARRIER_INFO]({ commit }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CARRIER_INFO, { loading: true });
        axios.get('/carriers/sign-up/info', { requestName: 'carrier info' }).then(({ data }) => {
          commit(mutations.UPDATE_CARRIER_INFO, { data });
          resolve(data);
        }).catch(() => {
          commit(mutations.UPDATE_CARRIER_INFO, { error: true });
          reject();
        }).finally(() => {
          commit(mutations.UPDATE_CARRIER_INFO, { loading: false });
        });
      });
    },
    [actions.GET_CARRIER_INSURANCE]({ commit }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CARRIER_INSURANCE, { loading: true });
        axios.get('/carriers/sign-up/insurance', { requestName: 'carrier insurance', apiErrMsg: true }).then(({ data }) => {
          commit(mutations.UPDATE_CARRIER_INSURANCE, { data });
          resolve(data);
        }).catch((error) => {
          commit(mutations.UPDATE_CARRIER_INSURANCE, { error: true });
          notificationError(error.response.data.detail);
          reject(error);
        }).finally(() => {
          commit(mutations.UPDATE_CARRIER_INSURANCE, { loading: false });
        });
      });
    },
    [actions.GET_CARRIER_CERTIFICATES]({ commit }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CARRIER_CERTIFICATES, { loading: true });
        axios.get('/carriers/sign-up/certificates', { requestName: 'carrier certificates', apiErrMsg: true }).then(({ data }) => {
          commit(mutations.UPDATE_CARRIER_CERTIFICATES, { data });
          resolve(data);
        }).catch((error) => {
          commit(mutations.UPDATE_CARRIER_CERTIFICATES, { error: true });
          notificationError(error.response.data.detail);
          reject(error);
        }).finally(() => {
          commit(mutations.UPDATE_CARRIER_CERTIFICATES, { loading: false });
        });
      });
    },
    [actions.EDIT_CARRIER_INFO]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CARRIER_INFO, { loading: true });
        axios.put('/carriers/info', data)
          .then((response) => {
            commit(mutations.UPDATE_CARRIER_INFO, { data });
            resolve(response);
          })
          .catch((error) => {
            commit(mutations.UPDATE_CARRIER_INFO, { error: true });
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CARRIER_INFO, { loading: false });
          });
      });
    },
    [actions.EDIT_CARRIER_INSURANCE]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        const formData = new FormData();

        formData.append('agent_name', data.agent_name);
        formData.append('agent_phone', data.agent_phone);
        formData.append('cargo_limit', data.cargo_limit);
        formData.append('deductible', data.deductible);
        formData.append('expiration_date', data.expiration_date);

        if (data.certificate_file) formData.append('certificate_file', data.certificate_file);

        // commit(mutations.UPDATE_CARRIER_INSURANCE, { loading: true });
        axios.put('/carriers/insurance', formData)
          .then((response) => {
            commit(mutations.UPDATE_CARRIER_INSURANCE, response.data);
            resolve(response);
          })
          .catch((error) => {
            commit(mutations.UPDATE_CARRIER_INSURANCE, { error: true });
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CARRIER_INSURANCE, { loading: false });
          });
      });
    },
    [actions.EDIT_CARRIER_CERTIFICATES]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        const formData = new FormData();

        if (data.w9_file) formData.append('w9_file', data.w9_file);
        if (data.usdot_file) formData.append('usdot_file', data.usdot_file);

        commit(mutations.UPDATE_CARRIER_CERTIFICATES, { loading: true });
        axios.put('/carriers/certificates', formData)
          .then((response) => {
            commit(mutations.UPDATE_CARRIER_CERTIFICATES, { formData });
            resolve(response);
          })
          .catch((error) => {
            commit(mutations.UPDATE_CARRIER_CERTIFICATES, { error: true });
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CARRIER_CERTIFICATES, { loading: false });
          });
      });
    },
  },
};
