import Vue from 'vue';
import axios from 'axios';
import { mutations, actions } from '@modules/auth/constants';

export default {
  namespaced: true,
  state: () => ({
    isLoading: false,
    carrierSignUpSteps: {
      1: {
        name: 'Carrier Information',
        number: 1,
        component: 'CarrierInformationForm',
      },
      2: {
        name: 'Email Verification',
        number: 2,
        component: 'EmailVerificationForm',
      },
      3: {
        name: 'Cargo Insurance',
        number: 3,
        component: 'CargoInsuranceForm',
      },
      4: {
        name: 'W9 and USDOT',
        number: 4,
        component: 'UsdotForm',
      },
    },
    carrierSignUpStep: 1,
    // TODO: this data shuld be used from dictionaries module
    states: {
      loading: false,
      data: [],
      error: false,
    },
    // END TODO: this data shuld be used from dictionaries module
  }),
  getters: {
    carrierSignUpStepsCount: (state) => Object.keys(state.carrierSignUpSteps).length,
  },
  mutations: {
    [mutations.TOGGLE_LOADING]: (state, loading) => {
      state.isLoading = loading;
    },
    [mutations.UPDATE_STATES_DATA]: (state, payload) => {
      state.states = {
        ...state.states,
        ...payload,
      };
    },
    [mutations.UPDATE_CARRIER_SIGN_UP_STEP]: (state, payload) => {
      state.carrierSignUpStep = payload;
    },
  },
  actions: {
    [actions.SIGN_IN]: ({
      dispatch,
    },
    {
      access,
      refresh,
      vm,
      onComplete,
    }) => {
      axios.defaults.headers.common.Authorization = `Bearer ${access}`;
      dispatch(actions.UPDATE_TOKENS, { access, refresh, vm });
      vm.$router.push({ name: vm.$constants.ROUTE_NAMES.LOADS }, onComplete);
    },
    [actions.RESET_PASSWORD]({ commit }, payload) {
      return new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, true);
        axios.post('/reset-forgot-password-verify', payload).then(() => {
          resolve();
        }).catch((error) => {
          if (error.response.status === 404) {
            reject(new Error('This email isn\'t registered.').message);
          } else {
            reject(error.response.data);
          }
        }).finally(() => {
          commit(mutations.TOGGLE_LOADING, false);
        });
      });
    },
    [actions.FORGOT_PASSWORD]({ commit }, { role, email }) {
      return new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, true);
        axios.post(`/${role}s/forgot-password`, { email }).then(() => {
          resolve();
        }).catch((error) => {
          if (error.response.status === 404) {
            reject(new Error('This email isn\'t registered.').message);
          } else {
            reject(error.response.data.detail);
          }
        }).finally(() => {
          commit(mutations.TOGGLE_LOADING, false);
        });
      });
    },
    [actions.SIGN_IN_FORM_SUBMIT]: ({ commit, dispatch, state }, {
      email,
      password,
      role,
      vm,
    }) =>
      new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, !state.isLoading);
        axios.post(`/${role}s/sign-in`, { email, password }).then((response) => {
          const { access, refresh } = response.data;
          resolve(dispatch(actions.SIGN_IN, { refresh, access, vm }));
        }).catch((error) => {
          if (error.response && error.response.status === 400) {
            reject(error.response.data[0]);
          }
        }).finally(() => {
          commit(mutations.TOGGLE_LOADING, !state.isLoading);
        });
      }),
    [actions.SIGN_OUT]: ({ dispatch }, { vm }) => {
      delete axios.defaults.headers.common.Authorization;
      dispatch(actions.UPDATE_TOKENS, { remove: true, vm });
      vm.$router.push({ name: vm.$constants.ROUTE_NAMES.SIGN_IN });
    },
    [actions.SIGN_UP]: ({ commit, state, dispatch }, { role, data, vm }) =>
      new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, !state.isLoading);
        axios.put(`/${role}s/sign-up`, { ...data }).then((response) => {
          const { access, refresh } = response.data;
          resolve(dispatch(actions.SIGN_IN, { refresh, access, vm }));
        }).catch((error) => {
          reject(error.response.data);
        }).finally(() => {
          commit(mutations.TOGGLE_LOADING, !state.isLoading);
        });
      }),
    [actions.GET_STATES]: ({ commit, state }) => {
      commit(mutations.UPDATE_STATES_DATA, {
        error: false,
      });

      if (!state.states.data.length) {
        state.states.loading = true;
        axios.get('/states', { requestName: 'states' }).then((response) => {
          commit(mutations.UPDATE_STATES_DATA, {
            data: response.data,
            error: false,
          });
        }).catch(() => {
          commit(mutations.UPDATE_STATES_DATA, {
            error: true,
            data: [],
          });
        }).finally(() => {
          commit(mutations.UPDATE_STATES_DATA, {
            loading: false,
          });
        });
      }
    },
    [actions.UPDATE_TOKENS]: (
      _,
      {
        access,
        refresh,
        vm,
        remove = false,
      },
    ) => {
      if (remove) {
        vm.$cookie.delete('sessionId');
        vm.$cookie.delete('refreshToken');
      } else {
        vm.$cookie.set('sessionId', access, { expires: 14 });
        vm.$cookie.set('refreshToken', refresh, { expires: 14 });
      }
    },
    [actions.UPDATE_TEMP_TOKEN]: (_, { access, remove }) => {
      if (remove) {
        Vue.cookie.delete('tempAccessToken');
      } else {
        Vue.cookie.set('tempAccessToken', access, { expires: 14 });
      }
    },
    [actions.UPDATE_CARRIER_SIGN_UP_STEP]: ({ commit, dispatch }, { step, access, remove = false }) => {
      if (remove) {
        localStorage.removeItem('carrier-registarion-step');
        dispatch(actions.UPDATE_TEMP_TOKEN, { remove: true });
        commit(mutations.UPDATE_CARRIER_SIGN_UP_STEP, 1);
      } else {
        localStorage.setItem('carrier-registarion-step', step);
        dispatch(actions.UPDATE_TEMP_TOKEN, { access });
        commit(mutations.UPDATE_CARRIER_SIGN_UP_STEP, step);
      }
    },
    [actions.CARRIER_INFORMATION_FORM_SUBMIT]: ({ commit, dispatch, state }, data) =>
      new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, !state.isLoading);
        axios.post('carriers/sign-up/info', { ...data })
          .then((response) => {
            const { access } = response.data;
            axios.defaults.headers.common.Authorization = `Bearer ${access}`;
            dispatch(actions.UPDATE_CARRIER_SIGN_UP_STEP, { step: 2, access });
            resolve();
          }).catch((error) => {
            delete axios.defaults.headers.common.Authorization;
            if (error.response && error.response.data) reject(error.response.data);
          }).finally(() => {
            commit(mutations.TOGGLE_LOADING, !state.isLoading);
          });
      }),
    [actions.VERIFY_CARRIER_EMAIL]: ({ commit, dispatch, state }, data) =>
      new Promise((resolve, reject) => {
        commit(mutations.TOGGLE_LOADING, !state.isLoading);
        axios.post('carriers/verify', { ...data })
          .then(() => {
            dispatch(actions.UPDATE_CARRIER_SIGN_UP_STEP, { step: 3, access: Vue.cookie.get('tempAccessToken') });
            resolve();
          }).catch((error) => {
            if (error.response && error.response.data) reject(error.response.data);
          }).finally(() => {
            commit(mutations.TOGGLE_LOADING, !state.isLoading);
          });
      }),
    [actions.RESEND_EMAIL_TO_CARRIER]: () =>
      new Promise((resolve, reject) => {
        axios.get('carriers/sign-up/info', { params: { verify: true } }).then(() => {
          resolve();
        }).catch((error) => {
          if (error.response && error.response.data) reject(error.response.data);
        });
      }),
    [actions.CARGO_INSURANCE_FORM_SUBMIT]: ({ commit, state, dispatch }, data) =>
      new Promise((resolve, reject) => {
        const formData = new FormData();

        formData.append('agent_name', data.agent_name);
        formData.append('agent_phone', data.agent_phone);
        formData.append('cargo_limit', data.cargo_limit);
        formData.append('certificate_file', data.certificate_file);
        formData.append('deductible', data.deductible);
        formData.append('expiration_date', data.expiration_date);

        commit(mutations.TOGGLE_LOADING, !state.isLoading);

        axios.post('/carriers/sign-up/insurance', formData)
          .then(() => {
            dispatch(actions.UPDATE_CARRIER_SIGN_UP_STEP, { step: 4, access: Vue.cookie.get('tempAccessToken') });
            resolve();
          })
          .catch((error) => {
            if (error.response && error.response.data) reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.TOGGLE_LOADING, !state.isLoading);
          });
      }),
    [actions.USDOT_W9_FORM_SUBMIT]: ({ commit, state, dispatch }, { data, vm }) =>
      new Promise((resolve, reject) => {
        const formData = new FormData();

        formData.append('usdot_file', data.usdot_file);
        formData.append('usdot_value', data.usdot_value);
        formData.append('w9_file', data.w9_file);

        commit(mutations.TOGGLE_LOADING, !state.isLoading);

        axios.post('/carriers/sign-up/certificates', formData)
          .then((response) => {
            const { access, refresh } = response.data;
            dispatch(actions.SIGN_IN, {
              refresh,
              access,
              vm,
              onComplete: () => {
                dispatch(actions.UPDATE_CARRIER_SIGN_UP_STEP, { remove: true });
              },
            });

            resolve();
          })
          .catch((error) => {
            dispatch(actions.UPDATE_TOKENS, { remove: true, vm });
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.TOGGLE_LOADING, !state.isLoading);
          });
      }),
  },
};
