<template>
  <v-app>
    <dispatch-drawer
      v-model="isDispatchDrawerOpened"
      :load-id="isDispatchDrawerLoadId"
      :load-status="isDispatchDrawerLoadStatus"
      :is-opened-from-loads-list="isDispatchDrawerOpenedFromList"
      @dispatch-drawer:close="closeDispatchDrawer"
      @dispatch-drawer:toggle="toggleDispatchDrawer"
    />
    <navigation v-if="!isMobile" />
    <navigation-mobile v-else />
    <slot></slot>
  </v-app>
</template>

<script>
import Navigation from '@components/Navigation';
import DispatchDrawer from '@components/DispatchDrawer';
import NavigationMobile from '@components/NavigationMobile';

export default {
  name: 'LayoutDefault',
  components: {
    Navigation,
    DispatchDrawer,
    NavigationMobile,
  },
  data() {
    return {
      isDispatchDrawerOpened: false,
      isDispatchDrawerLoadId: null,
      isDispatchDrawerLoadStatus: null,
      isDispatchDrawerOpenedFromList: true,
    };
  },
  computed: {
    isMobile() {
      return this.$vuetify.breakpoint.mobile;
    },
  },
  methods: {
    openDispatchDrawer({ loadId, loadStatus, fromList = true }) {
      this.isDispatchDrawerLoadId = loadId;
      this.isDispatchDrawerLoadStatus = loadStatus;
      this.isDispatchDrawerOpenedFromList = fromList;
      this.isDispatchDrawerOpened = true;
    },
    closeDispatchDrawer() {
      this.toggleDispatchDrawer(false);
    },
    toggleDispatchDrawer(opened) {
      if (!opened) {
        this.isDispatchDrawerLoadId = null;
        this.isDispatchDrawerLoadStatus = null;
      }
      this.isDispatchDrawerOpened = opened;
    },
  },
  mounted() {
    this.$root.$on('loads-list-item:dispatch-drawer-open', this.openDispatchDrawer);
  },
  beforeDestroy() {
    this.$root.$off('loads-list-item:dispatch-drawer-open', this.openDispatchDrawer);
  },
};
</script>
