import Vue from 'vue';
import Vuex from 'vuex';

import dictionaries from '@/store/dictionaries';
import { store as loads } from '@modules/loads';
import { store as auth } from '@modules/auth';
import { store as drivers } from '@modules/drivers';
import { store as dispatchers } from '@modules/dispatchers';
import { store as contacts } from '@modules/contacts';
import { store as dispatcher } from '@modules/dispatcher';
import { store as profile } from '@modules/profile';
import { store as settings } from '@modules/settings';
import { store as inspection } from '@modules/inspection';

import { getUserRole, getUserId } from '@utils/helpers/getUserTokenField';
import { actions, mutations } from '@/store/constants';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userRole: null,
    userId: null,
  },
  mutations: {
    [mutations.UPDATE_USER_ROLE](state, role) {
      state.userRole = role;
    },
    [mutations.UPDATE_USER_ID](state, id) {
      state.userId = id;
    },
  },
  actions: {
    [actions.SET_USER_ROLE]({ commit }, jwt) {
      const role = getUserRole(jwt);
      commit(mutations.UPDATE_USER_ROLE, role);
    },
    [actions.SET_USER_ID]({ commit }, jwt) {
      const userId = getUserId(jwt);
      commit(mutations.UPDATE_USER_ID, userId);
    },
  },
  modules: {
    auth,
    loads,
    dictionaries,
    drivers,
    dispatchers,
    contacts,
    dispatcher,
    profile,
    settings,
    inspection,
  },
});
