import axios from 'axios';
import { mutations, actions } from '@modules/contacts/constants';

export default {
  namespaced: true,
  state: () => ({
    contacts: {
      data: [],
      loading: false,
      error: false,
    },
    contactsEmails: [],
    contactsListDelete: [],
  }),
  getters: {
    CONTACTS_LIST_DELETE(state) {
      return state.contactsListDelete || [];
    },
  },
  mutations: {
    [mutations.UPDATE_CONTACTS_EMAILS](state, emails) {
      state.contactsEmails = [...emails];
    },
    [mutations.UPDATE_CONTACTS_DATA]: (state, payload) => {
      state.contacts = {
        ...state.contacts,
        ...payload,
      };
    },
    [mutations.UPDATE_CONTACTS_LIST_DELETE]: (state, contactId) => {
      state.contactsListDelete = contactId;
    },
    [mutations.CLEAR_CONTACTS_LIST_DELETE]: (state) => {
      state.contactsListDelete = [];
    },
  },
  actions: {
    [actions.CREATE_CONTACT]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.post('/contacts', data)
          .then(() => {
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CONTACTS_DATA, { loading: false });
          });
      });
    },
    [actions.EDIT_CONTACT]({ commit }, { data, id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.put(`/contacts/${id}`, data)
          .then(() => {
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CONTACTS_DATA, { loading: false });
          });
      });
    },
    [actions.DELETE_CONTACT]({ commit }, { id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.delete(`/contacts/${id}`)
          .then(() => {
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CONTACTS_DATA, { loading: false });
          });
      });
    },
    [actions.DELETE_CONTACTS]({ commit }, { ids }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.delete('/contacts', { data: { contacts_ids: ids } })
          .then(() => {
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_CONTACTS_DATA, { loading: false });
          });
      });
    },
    [actions.GET_CONTACTS]({ commit }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.get('/contacts', { requestName: 'contacts' }).then(({ data }) => {
          commit(mutations.UPDATE_CONTACTS_DATA, { data });
          resolve(data);
        }).catch((error) => {
          commit(mutations.UPDATE_CONTACTS_DATA, { error: true });
          reject(error);
        }).finally(() => {
          commit(mutations.UPDATE_CONTACTS_DATA, { loading: false });
        });
      });
    },
    [actions.GET_CONTACTS_EMAILS]({ commit }, query) {
      return new Promise((resolve, reject) => {
        const url = query ? `/contacts/emails?search=${query}` : '/contacts/emails';
        commit(mutations.UPDATE_CONTACTS_DATA, { loading: true });
        axios.get(url)
          .then((response) => {
            commit(mutations.UPDATE_CONTACTS_EMAILS, response.data);
            resolve(response);
          }).catch(() => {
            reject();
          });
      });
    },
  },
};
