<template>
  <transition name="notification">
    <transition-group
      name="notification"
      tag="div"
      class="admin-notifications"
      v-if="notifications.length"
    >
      <base-notifications-list-item
        v-for="notification in notifications"
        :key="notification.id"
        :notification="notification"
        @remove-notification="removeNotification"
      />
    </transition-group>
  </transition>
</template>

<script>
import EventBus from '@utils/eventBus';
import BaseNotificationsListItem from '@components/BaseNotificationsListItem';

export default {
  name: 'BaseNotificationsList',
  components: { BaseNotificationsListItem },
  data: () => ({
    notifications: [],
  }),
  mounted() {
    EventBus.$on(this.$constants.EVENTS.NOTIFICATION_EVENT, this.handleNotification);
  },
  methods: {
    handleNotification(notification) {
      this.notifications.push(notification);
    },
    removeNotification(notificationToRemove) {
      this.notifications = this.notifications.filter((notification) => notification.id !== notificationToRemove.id);
    },
  },
};
</script>

<style lang="scss">
.admin-notifications {
  position: fixed;
  bottom: 20px;
  right: 15px;
  max-width: 500px;
  height: auto;
  transition: height .2s ease;
  z-index: 10000;
}

.notification-move {
  transition: transform .3s;
}

.notification-enter-active, .notification-leave-active {
  transition: all 1s;
}

.notification-enter, .notification-leave-to {
  opacity: 0;
  transform: translateX(150px);
}
</style>
