import axios from 'axios';
import qs from 'qs';
import { identity } from 'ramda';
import LOAD_STATUSES from '@utils/constants/loadStatuses';
import LOAD_STATUSES_TITLE from '@utils/constants/loadStatusesTitle';
import {
  mutations,
  actions,
  getters,
  INITIAL_LOADS_STATUS,
} from '@modules/loads/constants';
import formatDate from '@utils/filters/formatDate';

export default {
  namespaced: true,
  state: () => ({
    loads: {
      data: {},
      loading: false,
      error: false,
    },
    load: {
      data: {},
      attachments: [],
      activities: [],
      loading: false,
      error: false,
    },
    query: {
      page: 1,
      status: INITIAL_LOADS_STATUS,
    },
    inspections: [],
    sort: {
      data: [],
      loading: false,
    },
    sortLoading: false,
  }),
  getters: {
    status(state) {
      return state.query.status
      && state.query.status.split('_').length > 1
        ? state.query.status.split('_').join(' ')
        : state.query.status;
    },
    queryString(state, { status }) {
      const query = {
        ...state.query,
        status,
      };

      return qs.stringify(query, { addQueryPrefix: true });
    },
    [getters.BOL_LINK](state) {
      return `https://givadispatch.com/loads/${state.load.data.id}/bol`;
    },
    [getters.INVOICE_LINK](state) {
      return `https://givadispatch.com/loads/${state.load.data.id}/invoice`;
    },
    [getters.RECEIPT_LINK](state) {
      return `https://givadispatch.com/loads/${state.load.data.id}/receipt`;
    },
  },
  mutations: {
    [mutations.UPDATE_LOADS_DATA]: (state, payload) => {
      state.loads = {
        ...state.loads,
        ...payload,
      };
    },
    [mutations.UPDATE_LOADS_QUERY](state, payload) {
      state.query = {
        ...state.query,
        ...payload,
      };
    },
    [mutations.RESET_LOAD_QUERY](state, { status }) {
      state.query = {
        page: 1,
        status,
      };
    },
    [mutations.UPDATE_LOAD_DRIVER](state, payload) {
      const loadStatus = payload.loadStatus.split('-').join('_');
      const loadIndex = state.loads.data[loadStatus]?.records.findIndex((load) => load.load_id === payload.loadId);
      state.loads.data[loadStatus].records[loadIndex].driver = payload.driverName;
    },
    [mutations.INCREMENT_LOADS_COUNT](state, payload) {
      state.loads.data[payload.loadStatus].count += 1;
    },
    [mutations.DECREMENT_LOADS_COUNT](state, payload) {
      state.loads.data[payload.loadStatus].count -= 1;
    },
    [mutations.REMOVE_LOAD_FROM_LOADS_LIST](state, payload) {
      const loadIndex = state.loads.data[payload.loadStatus].records.findIndex((load) => load.load_id === payload.loadId);
      state.loads.data[payload.loadStatus].records.splice(loadIndex, 1);
    },
    [mutations.UPDATE_LOAD_DATA](state, payload) {
      state.load = {
        ...state.load,
        ...payload,
      };
    },
    [mutations.UPDATE_LOAD_ACTIVTIES](state, payload) {
      state.load.activities = [...payload];
    },
    [mutations.UPDATE_LOADS_FILTERS_SORT_BY](state, payload) {
      state.sort = {
        ...state.sort,
        ...payload,
      };
    },
    [mutations.UPDATE_LOAD_PROP_DATA](state, { prop, data }) {
      state.load = {
        ...state.load,
        [prop]: {
          ...state.load[prop],
          ...data,
        },
      };
    },
    [mutations.UPDATE_LOAD_STATUS](state, status) {
      state.load = {
        ...state.load,
        data: {
          ...state.load.data,
          status,
        },
      };
    },
    [mutations.ADD_LOAD_VEHICLE_INSPECTION](state, inspection) {
      state.inspections.push(inspection);
    },
    [mutations.CLEAR_LOAD_VEHICLE_INSPECTION](state) {
      state.inspections = [];
    },
  },
  actions: {
    [actions.UPDATE_ROUTE_QUERY](_, { nextQuery, vm }) {
      vm.$router.push({ query: { ...vm.$route.query, ...nextQuery } }).catch(identity);
    },
    [actions.CLEAR_ROUTE_QUERY](_, { vm, status }) {
      vm.$router.push({ query: { page: 1, status } }).catch(identity);
    },
    [actions.DISPATCH_LOAD_TO_DRIVER]({ commit }, {
      loadStatus,
      driverName,
      driverId,
      loadId,
      deleteFromList,
    }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(loadId)}`, { driver: driverId })
          .then((response) => {
            if (deleteFromList) {
              commit(mutations.UPDATE_LOAD_DRIVER, { loadId, loadStatus, driverName });
            }

            if (loadStatus === LOAD_STATUSES.NEW && deleteFromList) {
              commit(mutations.DECREMENT_LOADS_COUNT, { loadStatus: LOAD_STATUSES.NEW });
              commit(mutations.INCREMENT_LOADS_COUNT, { loadStatus: LOAD_STATUSES.DISPATCHED });
              commit(mutations.REMOVE_LOAD_FROM_LOADS_LIST, { loadId, loadStatus });
            } else if (!deleteFromList && loadStatus === LOAD_STATUSES.NEW) {
              commit(mutations.UPDATE_LOAD_STATUS, LOAD_STATUSES.DISPATCHED);
            }
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.UNDISPATCH_LOAD_FROM_DRIVER]({ commit }, { loadId, loadStatus }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(loadId)}`, { driver: null, status: LOAD_STATUSES.NEW })
          .then((response) => {
            commit(mutations.DECREMENT_LOADS_COUNT, { loadStatus });
            commit(mutations.INCREMENT_LOADS_COUNT, { loadStatus: LOAD_STATUSES.NEW });
            commit(mutations.REMOVE_LOAD_FROM_LOADS_LIST, { loadId, loadStatus });
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.GET_LOADS]({ commit }, { query } = { query: '' }) {
      commit(mutations.UPDATE_LOADS_DATA, { loading: true });
      axios.get(`/loads${query}`, { requestName: 'loads' }).then(({ data }) => {
        commit(mutations.UPDATE_LOADS_DATA, { data });
      }).catch(() => {
        commit(mutations.UPDATE_LOADS_DATA, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_LOADS_DATA, { loading: false });
      });
    },
    [actions.GET_LOADS_FILTERS_SORT_BY]({ commit }, { key }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_LOADS_FILTERS_SORT_BY, { loading: true });
        if (!key) {
          commit(mutations.UPDATE_LOADS_FILTERS_SORT_BY, { data: [] });
        } else {
          axios.get(`/loads/filters?sort_by=${key}`, { requestName: 'sorting' })
            .then((response) => {
              let { data } = response;
              if (key.split('_')[1] === 'date') {
                data = data.map((dateUnit) => ({
                  ...dateUnit,
                  value_name: formatDate(dateUnit.value),
                }));
              }

              commit(mutations.UPDATE_LOADS_FILTERS_SORT_BY, { data });
              resolve(true);
            }).catch(() => reject()).finally(() => {
              commit(mutations.UPDATE_LOADS_FILTERS_SORT_BY, { loading: false });
            });
        }
      });
    },
    [actions.GET_BOL](_, { id }) {
      return new Promise((resolve, reject) => {
        const url = `https://givadispatch.com/loads/${encodeURIComponent(id)}/bol`;
        axios.get(url, { requestName: 'bol' }).then(() => {
          resolve(false);
        }).catch(() => reject());
      });
    },
    [actions.SEND_BOL](_, { id, recipients, attachments }) {
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${encodeURIComponent(id)}/send-bol`, { recipients, attachments })
          .then((response) => {
            resolve(response);
          }).catch((error) => {
            reject(error);
          });
      });
    },
    [actions.SEND_RECEIPT](_, { id, recipients }) {
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${encodeURIComponent(id)}/send-receipt`, { recipients })
          .then((response) => {
            resolve(response);
          }).catch((error) => {
            reject(error);
          });
      });
    },
    [actions.GET_INVOICE](_, { id }) {
      return new Promise((resolve, reject) => {
        const url = `https://givadispatch.com/loads/${encodeURIComponent(id)}/invoice`;
        axios.get(url, { requestName: 'invoice' }).then(() => {
          resolve(false);
        }).catch(() => reject());
      });
    },
    // eslint-disable-next-line
    [actions.SEND_INVOICE](_, { id, recipients, attachments, date, billTo }) {
      const data = {};
      if (recipients) {
        data.recipients = recipients;
      }
      if (attachments) {
        data.attachments = attachments;
      }
      if (date) {
        data.date = `${date} 00:00`;
      }
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${id}/send-invoice?bill_to=${billTo}`, data)
          .then((response) => {
            resolve(response);
          }).catch((error) => {
            reject(error);
          });
      });
    },
    [actions.SEND_ATTACHMENTS](_, { id, recipients, attachments }) {
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${id}/send-attachments`, { recipients, attachments })
          .then((response) => {
            resolve(response);
          }).catch((error) => {
            reject(error);
          });
      });
    },
    [actions.GET_LOAD]({ commit }, { id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_LOAD_DATA, { loading: true, error: false });
        axios.get(`/loads/${encodeURIComponent(id)}`, { requestName: `load "${id}"` }).then(({ data }) => {
          commit(mutations.UPDATE_LOAD_DATA, { data });
          resolve();
        }).catch((error) => {
          commit(mutations.UPDATE_LOAD_DATA, { error: error.response ? error.response.status : true });
          reject();
        }).finally(() => {
          commit(mutations.UPDATE_LOAD_DATA, { loading: false });
        });
      });
    },
    [actions.GET_LOAD_INSPECTIONS]({ commit }, { id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_LOAD_DATA, { loading: true });
        axios.get(`/internal/loads/${encodeURIComponent(id)}`, { requestName: `load "${id}"` }).then(({ data }) => {
          commit(mutations.UPDATE_LOAD_DATA, { data });
          resolve();
        }).catch(() => {
          commit(mutations.UPDATE_LOAD_DATA, { error: true });
          reject();
        }).finally(() => {
          commit(mutations.UPDATE_LOAD_DATA, { loading: false });
        });
      });
    },
    [actions.RESTORE_LOAD]({ commit }, { id, loadStatus }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(id)}/restore`).then(({ data }) => {
          commit(mutations.DECREMENT_LOADS_COUNT, { loadStatus });
          commit(mutations.INCREMENT_LOADS_COUNT, { loadStatus: data.status.toLowerCase() });
          commit(mutations.REMOVE_LOAD_FROM_LOADS_LIST, { loadId: id, loadStatus });
          resolve(true);
        }).catch(() => {
          reject();
        });
      });
    },
    [actions.GET_LOAD_VEHICLE_INTERNAL_INSPECTIONS]({ state, commit }, { loadId }) {
      commit(mutations.CLEAR_LOAD_VEHICLE_INSPECTION);
      state.load.data.vehicles.forEach(({
        make_name: make,
        model_name: model,
        type_name: type,
        id: vehicleId,
        year,
        color,
        lot,
        vin,
        inop,
        plate,
      }) => {
        const vehicleName = `${make} ${model}`;
        axios.get(`/internal/loads/${encodeURIComponent(loadId)}/vehicles/${encodeURIComponent(vehicleId)}/inspection`, { requestName: 'inspection' })
          .then(({ data }) => {
            commit(
              mutations.ADD_LOAD_VEHICLE_INSPECTION,
              {
                vehicle: {
                  name: vehicleName,
                  year,
                  color,
                  lot,
                  vin,
                  type,
                  inop,
                  plate,
                },
                pickup: data.pickup,
                delivery: data.delivery,
              },
            );
          });
      });
    },
    [actions.GET_LOAD_VEHICLE_INSPECTIONS]({ state, commit }, { loadId }) {
      commit(mutations.CLEAR_LOAD_VEHICLE_INSPECTION);
      state.load.data.vehicles.forEach(({
        make_name: make,
        model_name: model,
        type_name: type,
        id: vehicleId,
        year,
        color,
        lot,
        vin,
        inop,
        plate,
      }) => {
        const vehicleName = `${make} ${model}`;
        axios.get(`/loads/${encodeURIComponent(loadId)}/vehicles/${encodeURIComponent(vehicleId)}/inspection`, { requestName: 'inspection' })
          .then(({ data }) => {
            commit(
              mutations.ADD_LOAD_VEHICLE_INSPECTION,
              {
                vehicle: {
                  name: vehicleName,
                  year,
                  color,
                  lot,
                  vin,
                  type,
                  inop,
                  plate,
                },
                pickup: data.pickup,
                delivery: data.delivery,
              },
            );
          });
      });
    },
    [actions.GET_LOAD_ATTACHMENTS]({ commit }, { id }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_LOAD_DATA, { loading: true });
        axios.get(`/loads/${encodeURIComponent(id)}/attachments`, { requestName: `load "${id}" attachments` })
          .then(({ data: attachments }) => {
            commit(mutations.UPDATE_LOAD_DATA, { attachments });
            resolve(true);
          })
          .catch(() => {
            commit(mutations.UPDATE_LOAD_DATA, { error: true });
            reject();
          }).finally(() => {
            commit(mutations.UPDATE_LOAD_DATA, { loading: false });
          });
      });
    },
    [actions.GET_LOAD_ACTIVTIES]({ commit }, { id }) {
      commit(mutations.UPDATE_LOAD_DATA, { loading: true });
      return new Promise((resolve, reject) => {
        axios.get(`/loads/${encodeURIComponent(id)}/activities`, { requestName: `load "${id}" activities` })
          .then(({ data: activities }) => {
            commit(mutations.UPDATE_LOAD_ACTIVTIES, activities);
            resolve(true);
          })
          .catch(() => {
            commit(mutations.UPDATE_LOAD_DATA, { error: true });
            reject();
          }).finally(() => {
            commit(mutations.UPDATE_LOAD_DATA, { loading: false });
          });
      });
    },
    [actions.ADD_LOAD_ATTACHMENTS](_, { id, data }) {
      const formData = new FormData();

      data.forEach((file) => {
        formData.append('files', file);
      });

      return new Promise((resolve, reject) => {
        axios.post(`/loads/${encodeURIComponent(id)}/attachments`, formData)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error.response.data);
          });
      });
    },
    [actions.CREATE_LOAD](_, { data }) {
      return new Promise((resolve, reject) => {
        axios.post('/loads', data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.DELETE_LOAD_ATTACHMENT](_, { loadId, id }) {
      return new Promise((resolve, reject) => {
        axios.delete(`/loads/${encodeURIComponent(loadId)}/attachments/${encodeURIComponent(id)}`)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error.response.data);
          });
      });
    },
    [actions.UPDATE_LOAD_STATUS]({ commit }, { loadId, prevStatus, nextStatus }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(loadId)}`, { status: LOAD_STATUSES_TITLE[nextStatus] })
          .then(() => {
            commit(mutations.UPDATE_LOAD_STATUS, nextStatus);
            if (prevStatus) {
              commit(mutations.DECREMENT_LOADS_COUNT, { loadStatus: prevStatus });
              commit(mutations.INCREMENT_LOADS_COUNT, { loadStatus: nextStatus });
              commit(mutations.REMOVE_LOAD_FROM_LOADS_LIST, { loadId, loadStatus: prevStatus });
            }
            resolve();
          })
          .catch((error) => reject(error));
      });
    },
    [actions.UPDATE_LOAD_DATA](_, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(id)}`, data)
          .then(() => {
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.ADD_LOAD_VEHICLES](_, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${encodeURIComponent(id)}/vehicles`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.EDIT_LOAD_VEHICLE](_, { id, vehicleId, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(id)}/vehicles/${vehicleId}`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.DELETE_LOAD_VEHICLE](_, { loadId, id }) {
      return new Promise((resolve, reject) => {
        axios.delete(`/loads/${encodeURIComponent(loadId)}/vehicles/${id}`)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.ADD_LOAD_EXPENSES](_, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.post(`/loads/${encodeURIComponent(id)}/expenses`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.EDIT_LOAD_EXPENCE](_, { id, expenseId, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/loads/${encodeURIComponent(id)}/expenses/${encodeURIComponent(expenseId)}`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.DELETE_LOAD_EXPENSE](_, { loadId, id }) {
      return new Promise((resolve, reject) => {
        axios.delete(`/loads/${encodeURIComponent(loadId)}/expenses/${encodeURIComponent(id)}`)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.CREATE_MAKE](_, data) {
      return new Promise((resolve, reject) => {
        axios.post('/vehicles/makes/', data).then((res) => {
          resolve(res.data);
        }).catch((err) => {
          reject(err);
        });
      });
    },
    [actions.CREATE_MODEL](_, data) {
      return new Promise((resolve, reject) => {
        axios.post('/vehicles/models/', data).then((res) => {
          resolve(res.data);
        }).catch((err) => {
          reject(err);
        });
      });
    },
  },
};
