import axios from 'axios';
import { mutations, actions, getters } from '@modules/dispatchers/constants';

export default {
  namespaced: true,
  state: () => ({
    dispatchers: {
      data: [],
      loading: false,
      error: false,
    },
    dispatchersForFilters: {
      data: [],
      loading: false,
      error: false,
    },
    activeDispatcherId: null,
  }),
  getters: {
    [getters.ACTIVE_DISPATCHER](state) {
      const activeDispatcher = state.dispatchers.data.filter((dispatcher) => Number(dispatcher.id) === Number(state.activeDispatcherId));

      return activeDispatcher.length ? activeDispatcher[0] : null;
    },
    [getters.ACTIVE_DISPATCHER_ID](state) {
      return state.activeDispatcherId;
    },
  },
  mutations: {
    [mutations.ADD_DISPATCHER](state, payload) {
      state.dispatchers.data = [...state.dispatchers.data, payload];
    },
    [mutations.UPDATE_DISPATCHERS_DATA]: (state, payload) => {
      state.dispatchers = {
        ...state.dispatchers,
        ...payload,
      };
    },
    [mutations.UPDATE_DISPATCHERS_FOR_FILTERS_DATA](state, payload) {
      state.dispatchersForFilters = {
        ...state.dispatchersForFilters,
        ...payload,
      };
    },
    [mutations.UPDATE_ACTIVE_DISPATCHER_ID]: (state, payload) => {
      state.activeDispatcherId = payload;
    },
  },
  actions: {
    [actions.GET_DISPATCHERS]: ({ commit }) => {
      commit(mutations.UPDATE_DISPATCHERS_DATA, { loading: true });
      axios.get('/dispatchers/', { requestName: 'dispatchers' }).then(({ data }) => {
        commit(mutations.UPDATE_DISPATCHERS_DATA, { data });
        commit(mutations.UPDATE_ACTIVE_DISPATCHER_ID, data[0].id);
      }).catch(() => {
        commit(mutations.UPDATE_DISPATCHERS_DATA, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DISPATCHERS_DATA, { loading: false });
      });
    },
    [actions.GET_DISPATCHERS_FOR_FILTERS]: ({ commit }, { query } = { query: '' }) => {
      commit(mutations.UPDATE_DISPATCHERS_FOR_FILTERS_DATA, { loading: true });
      axios.get(`/dispatchers/${query}`, { requestName: 'dispatchers filter' }).then(({ data }) => {
        commit(mutations.UPDATE_DISPATCHERS_FOR_FILTERS_DATA, { data });
        commit(mutations.UPDATE_ACTIVE_DISPATCHER_ID, data[0].id);
      }).catch(() => {
        commit(mutations.UPDATE_DISPATCHERS_FOR_FILTERS_DATA, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DISPATCHERS_FOR_FILTERS_DATA, { loading: false });
      });
    },
    [actions.CHANGE_DISPATCHER_STATUS]({ dispatch }, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/dispatchers/${id}`, data)
          .then((response) => {
            resolve(response);
            dispatch(actions.GET_DISPATCHERS);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.INVITE_DISPATCHER]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_DISPATCHERS_DATA, { loading: true });
        axios.post('/dispatchers/', data)
          .then((response) => {
            commit(mutations.ADD_DISPATCHER, response.data);
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_DISPATCHERS_DATA, { loading: false });
          });
      });
    },
    [actions.EDIT_DISPATCHER](_, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/dispatchers/${id}`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error.response.data);
          });
      });
    },
    [actions.DELETE_DISPATCHER]({ dispatch }, { id }) {
      return new Promise((resolve, reject) => {
        axios.delete(`/dispatchers/${id}`)
          .then((response) => {
            dispatch(actions.GET_DISPATCHERS);
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
};
