<template>
  <router-link
    class="logo"
    tag="span"
    :to="{ name: $constants.ROUTE_NAMES.LOADS }"
  >
    <v-img
      alt="BOL"
      :src="require(`@images/icons/${src}.svg`)"
      :max-width="maxWidth"
    />
  </router-link>
</template>

<script>
export default {
  name: 'Logo',
  props: {
    kind: {
      type: String,
      default: 'default',
    },
    maxWidth: {
      type: String,
      default: 'auto',
    },
  },
  computed: {
    src() {
      return this.kind === 'default' ? 'logo' : `logo-${this.kind}`;
    },
  },
};
</script>

<style lang="scss" scoped>
  .logo {
    cursor: pointer;

    &.pointer-events-none {
      pointer-events: none;
    }
  }
</style>
