import Vue from 'vue';
import Router from 'vue-router';
import ROUTE_NAMES from '@utils/constants/route-names';
import store from '@/store';
import { actions } from '@/store/constants';
import routes from './routes';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: { name: ROUTE_NAMES.LOADS },
    },
    ...routes,
  ],
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});

router.beforeEach((to, _, next) => {
  if (to.path.startsWith('/loads')) {
    document.title = (to.params.id ? to.params.id : '') + to.meta.documentTitle;
  } else {
    document.title = to.meta.documentTitle;
  }
  if (to.meta.withNoAuth === true && Vue.cookie.get('sessionId')) {
    next({ name: ROUTE_NAMES.LOADS });
  } else if (to.meta.withNoAuth === undefined && !Vue.cookie.get('sessionId')) {
    next({ name: ROUTE_NAMES.SIGN_IN });
  } else if (to.meta.withNoAuth === undefined && Vue.cookie.get('sessionId')) {
    store.dispatch(actions.SET_USER_ROLE, Vue.cookie.get('sessionId'));
    store.dispatch(actions.SET_USER_ID, Vue.cookie.get('sessionId'));
    next();
  } else {
    next();
  }
});

export default router;
