<template>
  <transition name="notification">
    <div
      class="d-flex py-3 px-5 ma-1 border-radius"
      :class="notification.type"
    >
      <p
        class="body-1 white--text font-weight-medium"
        v-html="notification.message"
      >
        {{ notification.message }}
      </p>

      <span
        class="cursor-pointer ml-4"
        v-if="!notification.selfClosable"
        @click="$emit('remove-notification', notification, $el)"
      >
        <v-icon color="white body-2">mdi-close</v-icon>
      </span>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'BaseNotificationsListItem',
  props: {
    notification: {
      type: Object,
      required: true,
    },
  },
  mounted() {
    if (this.notification.selfClosable) {
      setTimeout(() => {
        this.$emit('remove-notification', this.notification, this.$el);
      }, this.notification.timeout);
    }
  },
};
</script>

<style lang="scss">
  .notification-enter-active, .notification-leave-active {
    transition: all 1s;
  }

  .notification-enter, .notification-leave-to {
    opacity: 0;
    transform: translateX(150px);
  }
</style>
