import axios from 'axios';
import { mutations, actions } from '@/store/dictionaries/constants';

export default {
  namespaced: true,
  state: () => ({
    states: {
      data: [],
      loading: false,
      error: false,
    },
    vehicles: {
      data: [],
      loading: false,
      error: false,
    },
    expenses: {
      data: [],
      loading: false,
      error: false,
    },
    paymentMethods: {
      data: [],
      loading: false,
      error: false,
    },
  }),
  mutations: {
    [mutations.UPDATE_STATES](state, payload) {
      state.states = {
        ...state.states,
        ...payload,
      };
    },
    [mutations.UPDATE_VEHICLES](state, payload) {
      state.vehicles = {
        ...state.vehicles,
        ...payload,
      };
    },
    [mutations.UPDATE_EXPENSES](state, payload) {
      state.expenses = {
        ...state.expenses,
        ...payload,
      };
    },
    [mutations.UPDATE_PAYMENT_METHODS](state, payload) {
      state.paymentMethods = {
        ...state.paymentMethods,
        ...payload,
      };
    },
  },
  actions: {
    [actions.GET_STATES]({ commit, state }) {
      commit(mutations.UPDATE_STATES, {
        error: false,
      });

      if (!state.states.data.length) {
        state.states.loading = true;
        axios.get('/states', { requestName: 'states' }).then(({ data }) => {
          commit(mutations.UPDATE_STATES, {
            data,
            error: false,
          });
        }).catch(() => {
          commit(mutations.UPDATE_STATES, {
            error: true,
            data: [],
          });
        }).finally(() => {
          commit(mutations.UPDATE_STATES, { loading: false });
        });
      }
    },
    [actions.GET_VEHICLES]({ commit, state }) {
      commit(mutations.UPDATE_VEHICLES, { error: false });

      if (!state.vehicles.data.length) {
        commit(mutations.UPDATE_VEHICLES, { loading: true });
        axios.get('/vehicles/types', { requestName: 'vehicle types' }).then(({ data }) => {
          commit(mutations.UPDATE_VEHICLES, {
            data,
            error: false,
          });
        }).catch(() => {
          commit(mutations.UPDATE_VEHICLES, {
            error: true,
            data: [],
          });
        }).finally(() => {
          commit(mutations.UPDATE_VEHICLES, { loading: false });
        });
      }
    },
    [actions.GET_EXPENSES]({ commit, state }) {
      commit(mutations.UPDATE_EXPENSES, { error: false });

      if (!state.expenses.data.length) {
        commit(mutations.UPDATE_EXPENSES, { loading: true });
        axios.get('/expenses/types', { requestName: 'expense types' }).then(({ data }) => {
          commit(mutations.UPDATE_EXPENSES, {
            data,
            error: false,
          });
        }).catch(() => {
          commit(mutations.UPDATE_EXPENSES, {
            error: true,
            data: [],
          });
        }).finally(() => {
          commit(mutations.UPDATE_EXPENSES, { loading: false });
        });
      }
    },
    [actions.GET_PAYMENT_METHODS]({ commit, state }) {
      commit(mutations.UPDATE_PAYMENT_METHODS, { error: false });

      if (!state.paymentMethods.data.length) {
        commit(mutations.UPDATE_PAYMENT_METHODS, { loading: true });
        axios.get('/payment-methods', { requestName: 'payment methods' }).then(({ data }) => {
          commit(mutations.UPDATE_PAYMENT_METHODS, {
            data,
            error: false,
          });
        }).catch(() => {
          commit(mutations.UPDATE_PAYMENT_METHODS, {
            error: true,
            data: [],
          });
        }).finally(() => {
          commit(mutations.UPDATE_PAYMENT_METHODS, { loading: false });
        });
      }
    },
  },
};
