import axios from 'axios';
import { identity } from 'ramda';
import { notificationError } from '@services/notifications';

export const handleResponseError = (error) => {
  const hasResponse = Boolean(error.response);
  // eslint-disable-next-line
  if (!hasResponse) return Promise.reject({ response: { data: {} } });
  const isRequestCancelled = axios.isCancel(error);
  if (isRequestCancelled) return Promise.reject(error);
  const { requestName, method, apiErrMsg } = error.config;
  const isGetRequest = method === 'get';
  const isValidationError = error.response && error.response.status === 400;

  if (isGetRequest && !isValidationError && !apiErrMsg) {
    const wasRequestFor = requestName ? ` for ${requestName}` : '';
    const message = `Sorry, we could not get requested data${wasRequestFor}. Please try again or contact administrator.`;
    notificationError(message);
  }

  return Promise.reject(error);
};

export default (vm) => {
  let token;
  if (vm.cookie.get('tempAccessToken')) {
    token = vm.cookie.get('tempAccessToken');
  }

  if (vm.cookie.get('sessionId')) {
    token = vm.cookie.get('sessionId');
  }

  axios.defaults.baseURL = process.env.VUE_APP_API_URL;
  axios.defaults.timeout = 15000;
  axios.defaults.headers.common.Authorization = token ? `Bearer ${token}` : null;
  axios.interceptors.response.use(identity, handleResponseError);
};
