import axios from 'axios';
import { mutations, actions, getters } from '@modules/drivers/constants';

export default {
  namespaced: true,
  state: () => ({
    drivers: {
      data: [],
      loading: false,
      error: false,
    },
    driversForFilters: {
      data: [],
      loading: false,
      error: false,
    },
    driversForDispatchDrawer: {
      data: [],
      loading: false,
      error: false,
    },
    activeDriverId: null,
  }),
  getters: {
    [getters.ACTIVE_DRIVER](state) {
      const activeDriver = state.drivers.data.filter((driver) => Number(driver.id) === Number(state.activeDriverId));

      return activeDriver.length ? activeDriver[0] : null;
    },
    [getters.ACTIVE_DRIVER_ID](state) {
      return state.activeDriverId;
    },
  },
  mutations: {
    [mutations.ADD_DRIVER](state, payload) {
      state.drivers.data = [...state.drivers.data, payload];
    },
    [mutations.UPDATE_DRIVERS_DATA](state, payload) {
      state.drivers = {
        ...state.drivers,
        ...payload,
      };
    },
    [mutations.UPDATE_DRIVERS_FOR_FILTERS_DATA](state, payload) {
      state.driversForFilters = {
        ...state.driversForFilters,
        ...payload,
      };
    },
    [mutations.UPDATE_DRIVERS_FOR_DISPATCH_DRAWER](state, payload) {
      state.driversForDispatchDrawer = {
        ...state.driversForDispatchDrawer,
        ...payload,
      };
    },
    [mutations.UPDATE_ACTIVE_DRIVER_ID](state, payload) {
      state.activeDriverId = payload;
    },
  },
  actions: {
    [actions.GET_DRIVERS]: ({ commit }, { query } = { query: '' }) => {
      commit(mutations.UPDATE_DRIVERS_DATA, { loading: true });
      axios.get(`/drivers/${query}`, { requestName: 'drivers' }).then(({ data }) => {
        commit(mutations.UPDATE_DRIVERS_DATA, { data });
        commit(mutations.UPDATE_ACTIVE_DRIVER_ID, data[0].id);
      }).catch(() => {
        commit(mutations.UPDATE_DRIVERS_DATA, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DRIVERS_DATA, { loading: false });
      });
    },
    [actions.GET_DRIVERS_FOR_FILTERS]: ({ commit }, { query } = { query: '' }) => {
      commit(mutations.UPDATE_DRIVERS_FOR_FILTERS_DATA, { loading: true });
      axios.get(`/drivers/${query}`, { requestName: 'drivers filter' }).then(({ data }) => {
        commit(mutations.UPDATE_DRIVERS_FOR_FILTERS_DATA, { data });
        commit(mutations.UPDATE_ACTIVE_DRIVER_ID, data[0].id);
      }).catch(() => {
        commit(mutations.UPDATE_DRIVERS_FOR_FILTERS_DATA, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DRIVERS_FOR_FILTERS_DATA, { loading: false });
      });
    },
    [actions.GET_DRIVERS_FOR_DISPATCH_DRAWER]: ({ commit }, { query } = { query: '' }) => {
      commit(mutations.UPDATE_DRIVERS_FOR_DISPATCH_DRAWER, { loading: true });
      axios.get(`/drivers/${query}`, { requestName: 'drivers dispatch drawer' }).then(({ data }) => {
        commit(mutations.UPDATE_DRIVERS_FOR_DISPATCH_DRAWER, { data });
      }).catch(() => {
        commit(mutations.UPDATE_DRIVERS_FOR_DISPATCH_DRAWER, { error: true });
      }).finally(() => {
        commit(mutations.UPDATE_DRIVERS_FOR_DISPATCH_DRAWER, { loading: false });
      });
    },
    [actions.CHANGE_DRIVER_STATUS]({ dispatch }, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/drivers/${id}`, data)
          .then((response) => {
            resolve(response);
            dispatch(actions.GET_DRIVERS);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    [actions.INVITE_DRIVER]({ commit }, { data }) {
      return new Promise((resolve, reject) => {
        commit(mutations.UPDATE_DRIVERS_DATA, { loading: true });
        axios.post('/drivers/', data)
          .then((response) => {
            commit(mutations.ADD_DRIVER, response.data);
            resolve(true);
          })
          .catch((error) => {
            reject(error.response.data);
          })
          .finally(() => {
            commit(mutations.UPDATE_DRIVERS_DATA, { loading: false });
          });
      });
    },
    [actions.EDIT_DRIVER](_, { id, data }) {
      return new Promise((resolve, reject) => {
        axios.put(`/drivers/${id}`, data)
          .then((response) => {
            resolve(response);
          })
          .catch((error) => {
            reject(error.response.data);
          });
      });
    },
    [actions.DELETE_DRIVER]({ dispatch }, { id }) {
      return new Promise((resolve, reject) => {
        axios.delete(`/drivers/${id}`)
          .then((response) => {
            dispatch(actions.GET_DRIVERS);
            resolve(response);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
};
